

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App{
  /* border:2px solid red; */
  height: 20rem;
  text-align: center;
  background-color: #61dafb;
  opacity: .78;
}
.heading{
  font-weight: 800;
}
#select{
  /* border: 2px solid red; */
  width: 50%;
  padding: 1rem 3rem;
  border-radius: 10%;
  border: none;
margin: 2em 0em;
/* background-color: .; */
opacity: .8;
}
.opt{
  border: 2px solid red;
  padding: 3rem;
}
